
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
data(){
return {
userName:'',
password:'',
}
},
directives: {
TransferDom
},
components: {
XInput,
XButton,
Alert
},
methods:{
goback(){
history.go(-1);
},
login(){
var _this = this, userNum = 0,hasError = false,theUser='';
if(_this.userName != '' && _this.password != ''){
_this.userInfo.forEach(function(val){
if(val.userName == _this.userName){
if(val.password == _this.password){
userNum ++;
theUser = val;
}else{
hasError = true;
}
}
})
if(userNum > 0){
_this.showPlugin('登录成功！');
localStorage.setItem('user',JSON.stringify(theUser));
setTimeout(() => {
this.$router.push({ path: '/' })
}, 2000);
}else if(hasError){
_this.showPlugin('密码错误！')
}else{
_this.showPlugin('登录失败！')
}
}
},
showPlugin (text,alert) {
this.$vux.alert.show({
title: alert||'提示',
content: text||'',
onShow () {
},
onHide () {
}
})
},
showPluginAuto (text,alert) {
this.showPlugin(text,alert);
setTimeout(() => {
this.$vux.alert.hide()
}, 3000)
},
}
}



