
import Swiper from 'vux/src/components/swiper/swiper.vue'

const baseList = [{
url: 'javascript:',

img:'static/img/list4.jpg',
title: '送你一朵fua'
}, {
url: 'javascript:',

img:'static/img/list3.jpg',
title: '送你一辆车'
}, {
url: 'javascript:',
img: 'https://static.vux.li/demo/5.jpg', // 404
title: '送你一次旅行',

img:'static/img/list6.jpg',
}]
const urlList = baseList.map((item, index) => ({
url: 'http://m.baidu.com',
img: item.img,
fallbackImg: item.fallbackImg,
title: `${item.title}`
}))
export default {
data () {
return {
imgList:urlList,
swiper_index: 0,
}
},
components: {
Swiper
},
methods: {
onIndexChange (index) {
this.swiper_index = index
},
}
}



