
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'


export default{
name:'detail',
created () {
this.$method()
console.log(this.$route.params.id)
},
data(){
return {
id:0,
content:this.hotList[0],
note:0,
stars:0,
collect:1
}
},
computed:{
getStar:{
get: function () {
return this.content.star/2;
},
set: function () {}
},
getTag:{
get:function(){
return this.content.type.join().replace(/,/g,' / ');
},
set:function(){}
},
getContent:{
get: function () {
var id = this.$route.params.id;
console.log(id);
if(typeof id != 'undefined'){
id = parseInt(id);
this.content = this.hotList[id];
}
return this.content;
},
set: function () {}
},
getDetailId:{
get: function () {
var id = this.$route.params.id;
console.log(id);
if(typeof id != 'undefined'){
id = parseInt(id);
this.content = this.hotList[id];
}
return this.content;
},
set: function () {}
}
},
components: {
Flexbox,
FlexboxItem,
Divider,
Cell,
Rater,
Group,
XButton,
CellBox
}
}



