
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Panel from 'vux/src/components/panel/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
name:'list',
components: {
Panel,
ButtonTabItem,
ButtonTab,
Flexbox,
FlexboxItem,
LoadMore
},
methods: {
onImgError (item, $event) {
console.log(item, $event)
},
},
computed:{
getList:{
get: function () {
var _this = this;
this.hotList.forEach(function(item){
var obj = {
src:_this.$check(item.img),title:_this.$check(item.title),
desc:_this.$check(item.author),url:'/detail/'+item.id+'',fallbackSrc:''
};
_this.list.push(obj);
})
return this.list;
},
set: function () {}
}
},
data () {
return {
list: [],
imgList:[{
src:'/static/img/list2.jpg',
title:'最新资讯',
link:''
},
{
src:'/static/img/list1.jpg',
title:'新书速递',
link:''
},{
src:'/static/img/list5.jpg',
title:'精选读物',
link:''
}],
}
}
}



