
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
name:'user',
components: {
Group,
Cell,
CellFormPreview,
CellBox,
Badge,
Grid,
GridItem,
XButton,

},
created(){

},
directives: {
TransferDom
},
data(){
return {
list:[{name:'礼包',src:'static/img/box.png'},
{name:'消息',src:'static/img/msg.png'},
{name:'下载',src:'static/img/arrow.png'},
{name:'钱包',src:'static/img/bag.png'}],
selectType:0,
}
},
computed:{
userObj:{
get:function(){
var obj = localStorage.getItem('user');
if(typeof obj != 'undefined'){
obj = JSON.parse(obj)
}else{
obj = null;
}
return obj;
},
set:function(){}
}
},
methods:{
clickType:function(type){
console.log(type)
if(typeof type != 'undefined'){
this.selectType = type;
}
},
logout(){
localStorage.clear();
this.showLoading ();
setTimeout(() => {
this.$router.push({ path: '/login' })
}, 1000);
},
showLoading () {
this.$vux.loading.show({
text: 'Loading'
})
setTimeout(() => {
this.$vux.loading.hide()
}, 2000)
},
}
}



